.benefits {
    position: relative;
}
.benefits::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
}

.benefits .title,
.benefit h2,
.benefit p {
    color: #ffffff;
    text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.5);
}
.benefits .title {
    margin-bottom: 60px !important;
}

.benefit img {
    width: 120px;
    background-color: #ffffff;
    margin-bottom: 20px;
}




@media (min-width: 1024px) {
    .benefits .container {
        width: 960px;
    }
}