.has-bg-img {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.has-overlay {
    position: relative;
}
.has-overlay::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}