.installation-video  {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.installation-video iframe {
    width: 100%;
    min-height: 230px;
    border-radius: 8px;
}

@media (min-width: 1024px) {
    .installation-video iframe {
        /* width: 65%; */
        min-height: 450px;
    }
}