.model {
    position: relative;
    width: 100%;
    min-height: 40vh;
    display: inline-block;
    padding: 0rem 0.75rem 0.75rem 0.75rem;
    background-color: #fafafa; 
    display: flex;
    justify-content: center;
    padding: 2.5rem 0rem;  
    overflow: hidden; 
}
.model article {
    z-index: +1;
}
.model h2,
.model h3 {
    width: 80%;
    margin: 0 auto;
    color: #000000;
    text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
} 
.model h3 {
    margin-bottom: 30px;
}
.model img {
    width: 70%;
    margin-top: -60px;
}
.model .model-features {
    position: relative;
}
.model .model-features {
    position: absolute;
    bottom: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fafafa;
    transition: bottom 0.5s ease-in-out;
}
.model.active .model-features,
.model:hover .model-features {
    bottom: 0;
    padding: 1rem;
}
.model:hover .model-features h2 {
    color: #333333;
    margin-bottom: 30px;
}
.model-features .features {
    overflow-y: auto;
    max-height: 80%;
}
.model-features .tag {
    width: 100%;
    display: block !important;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
    background-color: #000000 !important;
}
@media (min-width: 1024px) {
    .model h3 {
        margin-bottom: 15px;
    }
    .model img {
        margin-top: -80px;
    }
    .model-features .tag {
        width: 50%;
    }
}