.intro {
    text-align: center;
}
.intro-device {
    position: relative;
    width: 40%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
}
.intro-des {
    margin-bottom: 30px;
}

@media (min-width: 1024px){
    .intro {
        text-align: left;
    }
    .intro .title,
    .intro .subtitle,
    .intro-des {
        width:45%;
    }

    .intro-device {
        position: absolute;
        top: calc(50% - 290px);
        right: 5%;
        width: 450px;
    }
}