.footer ul {
    list-style: none;
}

@media (min-width: 1024px) {
    .footer .menu-list li a {
        padding: .5rem 0rem;
    }
    .footer .custom-container {
        width: 800px;
    }
}
.intro {
    text-align: center;
}
.intro-device {
    position: relative;
    width: 40%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
}
.intro-des {
    margin-bottom: 30px;
}

@media (min-width: 1024px){
    .intro {
        text-align: left;
    }
    .intro .title,
    .intro .subtitle,
    .intro-des {
        width:45%;
    }

    .intro-device {
        position: absolute;
        top: calc(50% - 290px);
        right: 5%;
        width: 450px;
    }
}
.installation {
    
}
.installation .column {
    min-height: 50vh;
}
.installation .left-split {
    background-position: left;
}
.installation .right-split {
    /* background-color: #0e1728; */
    background-color: #000000;
}
.installation .text-container {
    max-width: 100%;   
    margin-left: auto;
    margin-right: auto;
}

.installation .text-container .title,
.installation .text-container .subtitle {
    color: #ffffff;
}
.installation .text-container .title {
    margin-top: 0px;
    margin-bottom: 50px;
}

@media (min-width: 1024px) {
    .installation .text-container {
        max-width: 350px;   
    }
    .installation .text-container .title {
        margin: 50px;
    }
}
.installation-video  {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.installation-video iframe {
    width: 100%;
    min-height: 230px;
    border-radius: 8px;
}

@media (min-width: 1024px) {
    .installation-video iframe {
        /* width: 65%; */
        min-height: 450px;
    }
}
.benefits {
    position: relative;
}
.benefits::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
}

.benefits .title,
.benefit h2,
.benefit p {
    color: #ffffff;
    text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.5);
}
.benefits .title {
    margin-bottom: 60px !important;
}

.benefit img {
    width: 120px;
    background-color: #ffffff;
    margin-bottom: 20px;
}




@media (min-width: 1024px) {
    .benefits .container {
        width: 960px;
    }
}
.model {
    position: relative;
    width: 100%;
    min-height: 40vh;
    display: inline-block;
    padding: 0rem 0.75rem 0.75rem 0.75rem;
    background-color: #fafafa; 
    display: flex;
    justify-content: center;
    padding: 2.5rem 0rem;  
    overflow: hidden; 
}
.model article {
    z-index: +1;
}
.model h2,
.model h3 {
    width: 80%;
    margin: 0 auto;
    color: #000000;
    text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
} 
.model h3 {
    margin-bottom: 30px;
}
.model img {
    width: 70%;
    margin-top: -60px;
}
.model .model-features {
    position: relative;
}
.model .model-features {
    position: absolute;
    bottom: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fafafa;
    -webkit-transition: bottom 0.5s ease-in-out;
    transition: bottom 0.5s ease-in-out;
}
.model.active .model-features,
.model:hover .model-features {
    bottom: 0;
    padding: 1rem;
}
.model:hover .model-features h2 {
    color: #333333;
    margin-bottom: 30px;
}
.model-features .features {
    overflow-y: auto;
    max-height: 80%;
}
.model-features .tag {
    width: 100%;
    display: block !important;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
    background-color: #000000 !important;
}
@media (min-width: 1024px) {
    .model h3 {
        margin-bottom: 15px;
    }
    .model img {
        margin-top: -80px;
    }
    .model-features .tag {
        width: 50%;
    }
}
.sm-icon img {
    width: 40px;
    margin-right: 20px;
}

