.installation {
    
}
.installation .column {
    min-height: 50vh;
}
.installation .left-split {
    background-position: left;
}
.installation .right-split {
    /* background-color: #0e1728; */
    background-color: #000000;
}
.installation .text-container {
    max-width: 100%;   
    margin-left: auto;
    margin-right: auto;
}

.installation .text-container .title,
.installation .text-container .subtitle {
    color: #ffffff;
}
.installation .text-container .title {
    margin-top: 0px;
    margin-bottom: 50px;
}

@media (min-width: 1024px) {
    .installation .text-container {
        max-width: 350px;   
    }
    .installation .text-container .title {
        margin: 50px;
    }
}